@import "normalize";
@import "settings";



//global styles
body,html{
  height: 100%;
  min-height: 100%;
  color: $black;
  @include museoSansCyrl(500);
  font-size: 15px;
  line-height: 22px;
}
h1,h2,h3,h4,h5,h6{
  padding: 0;
  margin: 0;
}
body{
  padding-top: 44px;
  background-color: #f0f0f0;
  @media screen and (min-width: 1280px){
    padding-top: 61px;
  }
}
button{
  border: none;
  background: none;
  cursor: pointer;
  padding: 0;
  &:focus{
    outline: none;
  }
}
a{
  text-decoration: none;
}
.owl-carousel .owl-item img{
  width: auto;
}

.main__title{
  margin: 5px 0 10px;
  line-height: normal;
  text-align: center;
  font-size: 24px;

  @media screen and (min-width: 1280px){
    text-align: left;
    font-size: 30px;
  }
}
.at-resp-share-element .at4-share-count-container{
  float:none !important;
}
.at-resp-share-element .at-share-btn .at-icon-wrapper{
  position: relative;
  top: 5px
}
